import React from 'react'
import { Field, reduxForm } from 'redux-form'
import {
    Alert, Col, Row, FormGroup, Label, Button,
    Card, CardHeader, CardBody, CardFooter
} from 'reactstrap';

const validate = values => {
    const errors = {}
    if (!values.codbar) {
        errors.codbar = 'Obrigatório'
    }
    if (!values.produto) {
        errors.produto = 'Obrigatório'
    }
    if (!values.categoria) {
        errors.categoria = 'Obrigatório'
    }
    return errors
}

const FormProduto = (props) => {
    const { handleSubmit, show, error, pristine, submitting, initialValues } = props
    return (
        show &&
        <div className="animated fadeIn">
            <form onSubmit={handleSubmit} className="form" >
                <Col xs="12" sm="11" md="9" lg="8" xl="5" >
                    <Card>
                        <CardHeader>
                            <strong>{initialValues.produto}</strong>
                            <small> | Atualizar dados</small>
                        </CardHeader>
                        <CardBody>
                            {error && <Row>
                                <Col xs="12">
                                    <Alert color="danger">{error}</Alert>
                                </Col>
                            </Row>}
                            <Row>
                                <Col xs="12">
                                    <FormGroup>
                                        <Label htmlFor="dados.codigos">Códigos | <small>Separe os códigos com virgula ','</small></Label>
                                        <Field type="text" name="dados.codigos" placeholder="codigo01, cod02, codQualquer"
                                            component="input" className="form-control" />
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Row>
                                <Col xs="7">
                                    <FormGroup>
                                        <Label htmlFor="dados.preco">Preço de venda</Label>
                                        <Field type="number" name="dados.preco" placeholder="00,00"
                                            pattern="[0-9]+([\.,][0-9]+)?" step="0.01"
                                            component="input" className="form-control" required />
                                    </FormGroup>
                                </Col>
                                <Col xs="5">
                                    <FormGroup>
                                        <Label htmlFor="dados.situacao">Situação</Label>
                                        <Field component="select" className="form-control"
                                            name="dados.situacao" >
                                            <option value={true} >Ativo</option>
                                            <option value={false}>Inativo</option>

                                        </Field>
                                    </FormGroup>
                                </Col>
                            </Row>
                            <Row>
                                <Col xs="6">
                                    <FormGroup>
                                        <Label htmlFor="dados.estoque.disponivel">Estoque disponível</Label>
                                        <Field type="number" name="dados.estoque.disponivel" placeholder="Estoque disponível"
                                            pattern="[0-9]+([\.,][0-9]+)?" step="0.01"

                                            component="input" className="form-control" />
                                    </FormGroup>
                                </Col>
                                <Col xs="6">
                                    <FormGroup>
                                        <Label htmlFor="dados.estoque.minimo">Estoque mínimo</Label>
                                        <Field type="number" name="dados.estoque.minimo" placeholder="Estoque mínimo"
                                            pattern="[0-9]+([\.,][0-9]+)?" step="0.01"
                                            component="input" className="form-control" />
                                    </FormGroup>
                                </Col>
                            </Row>


                        </CardBody>
                        <CardFooter>
                            <Field component={Button} name="submit" type="submit" size="sm"
                                disabled={submitting || pristine}
                                color="primary" >
                                <i className="fa fa-dot-circle-o"></i> Salvar
                            </Field>
                            <button onClick={() => props.onCancel()}
                                className="btn-square btn btn-danger btn-sm"> <i className="fa close"></i> Cancelar</button>

                        </CardFooter>
                    </Card>
                </Col>
            </form>

        </div>
    )
}

//, enableReinitialize: true
export default reduxForm({
    form: 'formProduto',
    validate,
    enableReinitialize: true
})(FormProduto)
